html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.unselectable {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Chrome/Safari/Opera */
  -khtml-user-select: none; /* Konqueror */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  not supported by any browser */
}

#root {
  height: 100%;
}

/* DESKTOP */
/* Common */
.container {
  display: flex;
  height: 100%;
  background-color: black;
}

.with-image-background {
  background-size: cover;
  background: linear-gradient();
}

.after-header {
  height: 87% !important;
}

.flex-column {
  flex-direction: column;
}

.flex-column-reverse {
  flex-direction: column-reverse;
}

header {
  display: flex;
  width: 100%;
  height: 13%;
  font-family: Bebas Neue;
  background-color: #000000;
  color: rgba(219, 32, 44, 1);
  font-size: 5vmax;
  justify-content: center;
  align-items: center;
  min-height: 40px;
}

.home-button {
  position: absolute;
  left: 1.67vmax;
  top: auto;

  border: 0;
  width: 3.33vmax;
  height: 3.33vmax;
  min-width: 32px;
  min-height: 32px;
  background-size: cover;
  cursor: pointer;
}

.circleButton {
  /* button properties */
  border-radius: 500px;
  border: 0;
  cursor: pointer;

  /* text properties */
  font-family: Bebas Neue;
  font-style: normal;
  font-weight: normal;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer {
  flex-basis: 16.4%;
}

/* Home screen */
.title-wrapper {
  flex-basis: 13%;
  display: flex;
  justify-content: center;
  align-items: center;

  font-family: Bebas Neue;
  color: rgba(219, 32, 44, 1);
  font-size: 5vmax;
}

.description-wrapper {
  display: flex;
  flex-basis: 62.36%;
  justify-content: center;
  align-items: center;

  font-family: Bebas Neue;
  color: white;
}

.size-description-wrapper {
  display: flex;
  width: 38.75%;
  height: 43%;
  font-size: 2.5vmax;
  text-align: center;
}

.start-button-wrapper {
  display: flex;
  flex-basis: 19.5%;
  justify-content: center;
  align-items: center;
}

#start-button {
  background: rgba(20, 20, 20, 1);
  width: 27.24%;
  height: 100%;
  border: none;

  font-family: Bebas Neue;
  font-style: normal;
  font-weight: normal;
  color: rgba(219, 32, 44, 1);
  font-size: 3.75vw;

  cursor: pointer;
}

.sessionModal {
  position: absolute;
  /* width: 21.875vw;
  height: 21.875vw; */
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.7); /* Black w/ opacity */

  display: flex;
}

.sessionModalContent {
  width: 420px;
  height: 420px;
  margin: auto;
  background: #141414;
  border-radius: 50px;
  display: flex;
  flex-flow: column;
}

.sessionModal img {
  flex-basis: 16%;
  width: 16%;
  /* height: 16%; */
  justify-self: center;
  align-self: center;
  margin-top: 16%;
}

.sessionModal p {
  position: relative;
  flex-basis: 8%;
  font-family: Bebas Neue;
  font-size: 30px;
  text-align: center;

  /* top: 42%; */
  margin: 0;
  margin-top: 9.5%;

  color: white;
}

.sessionModal .buttonsWrapper {
  flex-basis: 14%;
  margin-top: 21.4%;

  display: flex;
  justify-content: space-evenly;
}

.sessionModal button {
  width: 32%;
  font-family: Bebas Neue;
  font-size: 30px;
  border: 0;
  cursor: pointer;
}

#YesButtonSessionModal {
  background-color: white;
  color: black;
}

#NoButtonSessionModal {
  background-color: #db202c;
  color: white;
}

.installationHint {
  position: absolute;
  width: 100vw;
  /* height: 45vh; */
  height: auto;
  left: 0;
  bottom: 0;

  background-color: #100e0e;
  color: white;
  font-family: Bebas Neue;
  font-size: 2.5vmax;
  line-height: 3.75vmax;

  display: flex;
  justify-content: center;
}

.installationHint .closeButton {
  position: absolute;
  width: 1.66vmax;
  height: 1.66vmax;
  right: 1vw;
  top: 1vh;
  cursor: pointer;
}

.installationHint .contentWrapper {
  width: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
}

.installationHint ol {
  margin: 28px 0 0;
  padding-left: 2vmax;
}

.installationHint li img {
  width: 2.5vmax;
  height: 2.5vmax;
  margin: 0 15px;
}

.installationHint form {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.installationHint form input {
  width: 1.25vmax;
  height: 1.25vmax;
  vertical-align: middle;
}

.installationHint form label {
  font-size: 1.5vmax;
  margin: -1vw 0 0 0;
}

.installationHint .circleButton {
  /* button properties */
  width: 5.73vmax;
  height: 5.73vmax;
  /* max-width: 140px;
    max-height: 140px; */
  background-color: #470e0e;
  align-self: center;
  margin-bottom: 10px;

  /* text properties */
  font-size: 2.6vmax;
  color: #db202c;
}

/* Question screen */
.divider {
  /* container properties */
  display: flex;
  align-items: center;
  justify-content: center;

  /* element properties */
  flex-basis: 10%;
}

.option-box {
  /* container properties */
  display: flex;
  flex-flow: column-reverse;

  /* element properties */
  flex-basis: 45%;

  background-size: cover;
  background-position-x: center;

  cursor: pointer;
}

.text-container {
  /* container properties */
  display: flex;
  width: 100%;
  height: 25%;
  background: rgba(0, 0, 0, 0.56);

  /* text properties */
  font-family: Bebas Neue;
  font-style: normal;
  font-weight: normal;
  font-size: 3.33vw;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}

.divider .circleButton {
  /* button properties */
  width: 10vw;
  height: 10vw;
  /* max-width: 140px;
  max-height: 140px; */
  background: rgba(20, 20, 20, 1);

  /* text properties */
  font-size: 2.75vw;
  color: #db202c;
}

/* Result screen */
.image-container {
  /* background-color: cornsilk; */
  background-size: cover;
  position: absolute;
  top: 13%;
  right: 0px;
  bottom: 0px;
  left: 0px;
}

.bottom-box {
  /* background-color: darkgreen; */
  background: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  flex-basis: 51.9%;
  display: flex;
  justify-content: space-between;
  padding-left: 1.04vw;
  padding-right: 1.04vw;
  max-height: 560px;
  z-index: 3;
}

.info-container {
  flex-basis: 39%;

  font-family: Bebas Neue;
  color: white;

  justify-content: flex-start;
  align-content: flex-start;
  display: inline-flex;
  flex-direction: column;
}

.image-title-info-wrapper {
  display: none;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  font-family: Bebas Neue;
  color: white;
  width: 62.8%;
  margin-bottom: 5.13%;

  justify-content: flex-start;
  align-content: flex-start;
  flex-direction: column;
}

.description-container {
  flex-basis: 44.27%;
  font-family: Bebas Neue;
  color: white;
  /* background-color: slateblue; */

  display: flex;
  flex-flow: column;
}

.description-text-wrapper {
  flex-basis: 84.5%;
  display: flex;
}

.button-container {
  flex-basis: 15.5%;

  font-size: 2.08vw;
  color: rgba(229, 9, 20, 1);

  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.button-label {
  display: flex;
  margin-right: 1.04vw;
}

#similar-button {
  border-radius: 500px;
  border: 0;
  width: 3.91vw;
  height: 3.91vw;
  /* max-width: 140px;
    max-height: 140px; */
  min-width: 32px;
  min-height: 32px;
  background-size: cover;
  background-image: url(https://i.ibb.co/7CwHt7j/image-7.png);
}

.title {
  margin: 0;
  font-weight: normal;
  cursor: pointer;
}
h2 {
  font-size: 1.57vw;
  font-weight: normal;
  /* margin: 0; */
}
p {
  font-size: 1.57vw;
  font-weight: normal;
  /* margin: 0; */
}

.text-margin-top {
  margin-top: 1.35vw;
  margin-bottom: 0;
}

/* TABLET & MOBILE */
@media screen and (max-width: 1024px) and (orientation: portrait) {
  /* Common */
  .footer {
    display: none;
  }

  /* Home screen */
  .title-wrapper {
    flex-basis: 7.37% !important;
    font-size: 6.1vh !important;
  }

  .description-wrapper {
    flex-basis: 73.13% !important;
  }

  .size-description-wrapper {
    font-size: 3vh !important;
    width: 100% !important;
  }

  #start-button {
    width: 100% !important;
    font-size: 8.87vh !important;
  }

  .sessionModal p {
    font-size: 30px !important;
  }

  /* Question screen */
  #other-button {
    width: 10vh !important;
    height: 10vh !important;
    font-size: 2.75vh !important;
  }

  #ob2 {
    flex-flow: column;
  }

  .text-container {
    font-size: 3.33vh !important;
  }
}

/* MOBILE ONLY */
@media screen and (max-width: 768px) and (orientation: portrait) {
  /* Common */
  .container {
    flex-direction: column;
  }

  .after-header {
    height: 92.63% !important;
  }

  /* Home screen */
  .sessionModalContent {
    width: 320px;
    height: 320px;
  }

  .sessionModal .yesNoWrapper {
    margin-top: 30px !important;
  }

  /* Result screen */
  header {
    height: 7.37%;
    font-size: 6.1vh;
  }

  .image-container {
    position: relative !important;
    flex-basis: 30.8%;
    top: 0 !important;

    display: flex;
    flex-flow: column-reverse;
    background-size: cover !important;
    background-repeat: no-repeat;
  }

  .image-title-info-wrapper {
    display: block !important;
  }

  .image-title-info-wrapper h2 {
    font-size: 1.45vh !important;
  }

  .bottom-box {
    flex-flow: column;
    flex-basis: 69.2% !important;
    justify-content: start !important;
  }

  .bottom-title-info-wrapper {
    display: none;
  }

  .info-container {
    flex-basis: auto !important;
  }

  .description-container {
    flex-basis: auto !important;
  }

  .button-container {
    position: absolute;
    width: 100%;
    height: 9.7%;
    min-height: 87px;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 4.46vh !important;
    background-color: #000000;
  }

  #similar-button {
    width: 3.91vh;
    height: 3.91vh;
    margin-left: 6px;
    margin-right: 6px;
  }

  h1 {
    font-size: 7.5vh !important;
  }

  h2,
  p {
    font-size: 2.46vh !important;
  }

  .text-margin-top {
    margin-top: 1.35vh !important;
  }
}

/* TABLET ONLY */
@media screen and (min-width: 768px) and (max-width: 1024px) and (orientation: portrait) {
  /* Common */
  .container {
    flex-direction: column;
  }

  .after-header {
    height: 92.63% !important;
  }

  /* Result screen */
  header {
    height: 7.37%;
    font-size: 6.1vh;
  }

  .image-container {
    position: relative !important;
    flex-basis: 44.02% !important;
    top: 0 !important;

    display: flex;
    flex-flow: column-reverse;
  }

  .image-title-info-wrapper {
    display: block !important;
  }

  .image-title-info-wrapper h2 {
    font-size: 1.45vh !important;
  }

  .bottom-box {
    flex-flow: column;
    flex-basis: 69.2% !important;
    justify-content: start !important;
  }

  .bottom-title-info-wrapper {
    display: none;
  }

  .info-container {
    flex-basis: auto !important;
  }

  .description-container {
    flex-basis: auto !important;
  }

  .button-container {
    position: absolute;
    width: 100%;
    height: 9.7%;
    min-height: 87px;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 4.46vh !important;
    background-color: #000000;
  }

  #similar-button {
    width: 3.91vh;
    height: 3.91vh;
    margin-left: 6px;
    margin-right: 6px;
    min-width: 55px !important;
    min-height: 55px !important;
  }

  h1 {
    font-size: 7.5vh !important;
  }

  h2,
  p {
    font-size: 2.46vh !important;
  }

  .text-margin-top {
    margin-top: 1.35vh !important;
  }
}
